.App{
    max-width: 1280px;
    margin: 0 auto;
}
#homeComponent{
    padding-bottom: 50px;
    #homeHeaderImage{
        background-image: url('./images/img_1.jpg');
        background-size: cover;
        background-repeat: no-repeat;
        background-position: center center;
        min-height: 565px;
        display: flex;
        justify-content: center;
        align-items: center;
        background-color: #ffe895;

        @media (max-width: 768px) {
            align-items: flex-end;
          }

        #textOverImage{
            p{
                color: #6f1c1c;
                font-size: 30px;
                font-family: straight;
                font-weight: bold;
                &:first-of-type{
                    font-size: 50px;
                }
            }
        }
    }

    #homeNavigationContainer{
        display: flex;
        align-items: center;
        background-color: white;
        box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
        margin: 20px 0;
        @media (max-width: 768px) {
            flex-direction: column;
        }
        p{
            flex-grow: 1;
            color: black;
            font-size: 20px;
            margin: 0;
            padding: 10px 0;
            font-family: cursive;
            cursor: pointer;
            @media (max-width: 768px) {
                font-size: 15px;
                width: 100%;
            }
            &.active{
                pointer-events: none;
                background-color: rgba(0, 0, 0, 0.1);
            }
        }
    }

    #homeMandalaImage{
        min-height: 100%;
        background-image: url('./images/invite_background.jpg');
        background-size: contain;
        // background-repeat: no-repeat;
        background-position: center center;
        display: flex;
        justify-content: center;
        align-items: center;
        background-color: #bbf1fe;
        @media (max-width: 768px) {
            background-size: cover;
        }
        #homeInvitationCard{
            margin: 100px 20px;
            padding: 20px 30px;
            background-color: transparent;
            font-family: cursive;
        }
    }

    #ashirwadContainer{
        display: flex;
        align-items: center;
        flex-wrap: wrap;
        .profileBorder{
            padding: 10px;
            width: 31%;
            @media (max-width: 768px) {
                width: 100%;
                margin-bottom: 20px;
            }

            .profileContainer{
                display: flex;
                flex-direction: column;
                justify-content: center;
                .profileImage{
                    height: 300px;
                    border-width:10px;  
                    border-style:groove;
                    img{
                        width: 100%;
                        height: 100%;
                    }
                }
                .profileDescription{
                    font-family: cursive;
                }
            }
        }
    }

    #footer{
        background-color: #6b6be6;
        display: flex;
        justify-content: center;
        align-items: center;
        padding: 20px;
        #hotelInfo{
            color: white;
            font-size: 20px;
            a{
                border: 1px solid white;
                background-color: transparent;
                color: white;
                font-size: 15px;
                padding: 5px 30px;
                cursor: pointer;
            }
        }
    }
}